/* ubuntu-mono-400normal - latin */
@font-face {
  font-family: 'Ubuntu Mono';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src:
    local('Ubuntu Mono Regular '),
    local('Ubuntu Mono-Regular'),
    url(ubuntu-mono-latin-400.9937c52e.woff2) format('woff2'), 
    url(ubuntu-mono-latin-400.fda3eddd.woff) format('woff'); /* Modern Browsers */
}

/* ubuntu-mono-400italic - latin */
@font-face {
  font-family: 'Ubuntu Mono';
  font-style: italic;
  font-display: swap;
  font-weight: 400;
  src:
    local('Ubuntu Mono Regular italic'),
    local('Ubuntu Mono-Regularitalic'),
    url(ubuntu-mono-latin-400italic.4cbe65be.woff2) format('woff2'), 
    url(ubuntu-mono-latin-400italic.c2cb57e6.woff) format('woff'); /* Modern Browsers */
}

