.veEditor {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.veVectorInteractionWrapper:focus {
  -webkit-box-shadow: 0 4px 2px -2px #9ecaed;
          box-shadow: 0 4px 2px -2px #9ecaed;
}

.taProductViewer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.RowViewSide,
.CircularViewSide {
  width: 100%;
}
.tg-editor-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.ve-clickable {
  cursor: pointer;
  opacity: 0.7;
}
.ve-clickable:hover {
  opacity: 1;
}
.ve-clickable-black {
  cursor: pointer;
  color: rgb(78, 78, 78);
}
.ve-clickable-black:hover {
  color: black;
}

.veRightClickTabMenu {
  opacity: 0.4;
}
.veRightClickTabMenu:hover {
  opacity: 1;
}

.reflex-layout > .reflex-element {
  overflow: hidden !important;
}
.reflex-layout.reflex-container {
  height: inherit !important;
}

.preview-mode-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;

  /* border: 1px solid black; */
}
.preview-mode-container > div {
  -webkit-box-shadow: 0 0 3px #ccc;
  box-shadow: 0 0 3px #ccc;
  padding: 2px;
}

.preview-mode-view-fullscreen {
  top: 1%;
  left: 1%;
  /* transform: translateX(-50%) translateY(-50%); */
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;

  /* opacity: .9; */
}

/* .preview-mode-container>div:hover .preview-mode-view-fullscreen {
 opacity: 1;
} */

.preview-mode-view-fullscreen:hover {
  background: #0e5a8a;
}

.bp3-menu-item span.bp3-tag {
  -webkit-transform: scale(0.8);
      -ms-transform: scale(0.8);
          transform: scale(0.8);
}
